import React, { FunctionComponent } from "react";

import { Container, Row, Col } from "react-bootstrap";
import { OverlayTrigger, Tooltip, Popover } from "react-bootstrap";
import { Upload, Download, PlusCircle, InfoCircle, Gear } from 'react-bootstrap-icons';

import { InfoBox } from "./InfoBox";
import { WorkspaceInfo } from "../model/WorkspaceInfo";

type IconBarProps = {
    onLoadFile: () => void;
    onNewRecipe: () => void;
    onSaveFile: () => void;
    onEditSettings: () => void;
    unsavedChanges: boolean;
    wsInfo: WorkspaceInfo;
};

export const IconBar: FunctionComponent<IconBarProps> = ({
    onLoadFile,
    onNewRecipe,
    onSaveFile,
    onEditSettings,
    unsavedChanges,
    wsInfo
}) => {
    return (
        <Container className="pl-0 ml-2 mr-2 p-0">
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="upload"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-upload">
                                Load recipes and settings
                            </Tooltip>
                        }>
                        <Upload
                            className="my-first-step-a"
                            style={{ color: "#ffffff", cursor: "pointer" }}
                            onClick={onLoadFile} />
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="download"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-download">
                                Save recipes and settings
                            </Tooltip>
                        }>
                        {unsavedChanges
                            ? <Download
                                className="text-warning"
                                style={{ cursor: "pointer" }}
                                onClick={onSaveFile} />
                            : <Download
                                style={{ color: "#ffffff40" }} />
                        }
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="new"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-new">
                                Add new recipe
                            </Tooltip>
                        }>
                        <PlusCircle
                            className="my-first-step-b"
                            style={{ color: "#ffffff", cursor: "pointer" }}
                            onClick={onNewRecipe} />
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="info"
                        placement="right"
                        overlay={
                            <Popover id="popover-info" className="quick-info">
                                <Popover.Title>Recipes information</Popover.Title>
                                <Popover.Content>
                                    <InfoBox wsInfo={wsInfo} />
                                </Popover.Content>
                            </Popover>
                        }>
                        <InfoCircle
                            style={{ color: "#ffffff", cursor: "pointer" }} />
                    </OverlayTrigger>
                </Col>
            </Row>
            <Row className="mb-4">
                <Col className="pr-0">
                    <OverlayTrigger
                        key="settings"
                        placement="right"
                        overlay={
                            <Tooltip id="tooltip-settings">
                                Settings
                            </Tooltip>
                        }>

                        <Gear style={{
                            color: "#ffffff",
                            cursor: "pointer"
                        }} onClick={onEditSettings} />
                    </OverlayTrigger>
                </Col>
            </Row>
        </Container>
    );
};
